.player-list{
  height: 50px;
  overflow: auto;
  border-radius: 2px;
  border: 1px solid #000;
  background-color: #2979ff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align:center;
      align-items:center;
}

.player-list li{
  display: inline-block;
  margin-right: 10px;
}

.correspondence{
  height: 150px;
  overflow: auto;
  border-radius: 2px;
  border: 1px solid #000;
  background-color: #2962ff;
}

.correspondence li{
  list-style: none;
  text-align: left;
}


