.tictactoe__xo{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.xo{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 400px;
  height: 400px;
}

.xo__item{
  font-size: 70px;
  line-height: 100%;
  text-align: center;
  cursor: pointer;
  width: calc(100% /3);
  height: calc(100% /3);
  background-color: #009688;
  border: 1px solid #212121;
  border-radius: 5px;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}

.xo__item:hover{
  background-color: #4db6ac;
  border-radius: 3px;
}


